import Observer from "../observer";

/**
 * 
 */
export default class Particle<T, E> extends Observer<T, E> {
  /**
   * id
   */
  id: number;
  constructor(id: number) {
    super();
    this.id = id;
  }

  /**
   * x
   * @param x
   */
  mx(x: number) {
    this.x = x;
  }
  /**
   * y
   * @param x
   */
  my(y: number) {
    this.y = y;
  }
  /**
   * rotation
   * @param x
   */
  mr(r: number) {
    this.rotation = r;
  }
}
